@font-face {
  font-family: 'Dela Gothic One', cursive;
  src: url('https://fonts.googleapis.com/css2?family=Dela+Gothic+One&display=swap');
}


.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  /* font-family: 'Dela Gothic One', cursive; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  width: 60%;
}

.btn {
  margin: 1em;
  padding: 0.6em 1em;
  background-color: #d2cb06;

  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
}

img {
  width: 20%;
}

p, ul {
  text-align: left;
}

ul {
  list-style: none;
  padding: 0;
}

span {
  text-transform: uppercase;
}
